import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import png from "@/util/3.gif";
import jwtDecode from "jwt-decode";
import jpg1 from "@/assets/image/produce1.jpg";
import jpg2 from "@/assets/image/produce2.jpg";
import jpg3 from "@/assets/image/produce3.jpg";
import HomeColor from "@/assets/tabBar/HomeIconColor.jpg";
import HomeGrey from "@/assets/tabBar/HomeIconGrey.jpg";
import task1 from "@/assets/tabBar/task1.jpg";
export default {
  setup() {
    const icon = {
      active: HomeColor,
      inactive: HomeGrey
    };
    const imageUrl1 = [task1];
    const image = [png];
    const show_1 = ref(false);
    const show_2 = ref(false);
    const show_3 = ref(false);
    const show_4 = ref(false);
    const show_5 = ref(false);
    const show_6 = ref(false);
    const show_8 = ref(false);
    const message = ref("");
    const active = ref(1);
    const router = useRouter();
    const show_7 = ref(true); // 初始时显示加载中效果
    const disPlay = url => {
      router.replace(`/ProduceDisplay/${url}`);
    };
    const images = [jpg1, jpg2, jpg3];
    var y = window.innerHeight * 0.74;
    var x = window.innerWidth * 0.75;
    const offset_produce = ref({
      x: x,
      y: y
    });
    const onClick = () => {
      router.replace("/produceAdd/1");
    };
    const produceList = ref([]);
    const value = ref("");
    const val = value.value;
    const onSearch = val => {
      show_7.value = true;
      fetchData(val);
    };
    onMounted(() => {
      fetchLoginToken();
      onSearch(val);
    });
    const token = localStorage.getItem("jwtToken"); // 从localStorage获取JWT令牌
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    let userId = "";
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        console.log(response.data.data);
        const decodedToken = jwtDecode(token);
        // 从解码后的令牌中获取特定的数据
        userId = decodedToken.id; // 从令牌中获取用户ID
        // 在这里处理登录令牌接口的响应
        // 如果需要执行一些特定的操作，可以在这里添加代码
      }).catch(error => {
        console.error("请求loginToken接口失败", error);
        router.replace("/login");
      });
    };
    const fetchData = val => {
      // 使用 axios 或 fetch 发起 API 请求，获取商品数据
      // 假设 API 路径为 '/api/products'
      // 在发起数据请求之前
      console.log("Before fetching data. produceList:", produceList.value);
      axios.get(`/api/nameSelectProduce?name=${val}`, {
        headers
      }).then(response => {
        // 请求成功，将获取的数据存储在 produceList.value 数组中
        produceList.value = response.data.data;
        console.log("Data fetched successfully. produceList:", produceList.value);
        show_7.value = false;
      }).catch(error => {
        console.error("Failed to fetch data:", error);
      });
    };
    let index_tem = "";
    let position_tem = "";
    let item_tem = "";
    const deleteItem = (index, position, item) => {
      if (item.founderId == userId) {
        // 重新赋值
        index_tem = index;
        position_tem = position;
        show_1.value = true;
      } else {
        show_5.value = true;
      }
    };
    const handleConfirm = () => {
      show_7.value = true;
      deleteProduce(index_tem, position_tem);
    };
    const handleCancel = () => {
      return;
    };
    const deleteProduce = (ProduceId, position) => {
      axios.delete(`/api/deleteProduce?id=${ProduceId}`, {
        headers
      }).then(response => {
        // 处理删除成功的逻辑
        console.log("数据删除成功", response);
        // 删除成功后重新加载页面
        produceList.value.splice(position, 1); // 从数组中移除商品项
        message.value = "删除成功!";
      }).catch(error => {
        // 处理删除失败的逻辑
        console.error("数据删除失败", error);
        message.value = "删除失败!";
      });
      show_8.value = true; // 显示
      // 等待两秒后执行关闭操作
      setTimeout(() => {
        show_8.value = false; // 关闭
      }, 1000); // 2000毫秒等于2秒
      show_7.value = false;
    };
    const buyItem = (index, position, item) => {
      axios.get(`/api/selectCredit?name=${item.customer}`, {
        headers
      }).then(response => {
        const nameId = response.data.data.nameId;
        if (nameId == userId) {
          index_tem = index;
          position_tem = position;
          item_tem = item;
          show_2.value = true;
        } else show_6.value = true;
      });
    };
    const checkConfirm = () => {
      show_7.value = true; // 初始时显示加载中效果
      buyProduce(index_tem, position_tem, item_tem);
    };
    const checkCancel = () => {
      return;
    };
    const buyProduce = (ProduceId, position, item) => {
      axios.get(`/api/selectCredit?name=${item.customer}`, {
        headers
      }).then(response => {
        // 处理获取成功的逻辑
        console.log("获取数据成功", response);
        const buyCredit = response.data.data.credit;
        if (buyCredit >= item.produceCredit) {
          // 发送 HTTP PUT 请求来完成购买操作
          axios.put(`/api/buyProduce?id=${ProduceId}`, null, {
            headers
          }).then(response => {
            // 处理完成成功的逻辑
            console.log("任务已保存成功", response.data);
            show_7.value = false;
            message.value = "购买成功！";
            item.produceNum = item.produceNum - 1;
          }).catch(error => {
            // 处理完成失败的逻辑
            console.error("数据完成失败", error);
            message.value = "购买失败！";
          });
        } else {
          show_3.value = true;
        }
      }).catch(error => {
        // 处理获取失败的逻辑
        console.error("获取数据失败", error);
      });
      show_8.value = true; // 显示
      // 等待两秒后执行关闭操作
      setTimeout(() => {
        show_8.value = false; // 关闭
      }, 1000); // 2000毫秒等于2秒
      show_7.value = false;
    };
    const goodItem = () => {
      show_4.value = true;
    };
    const formatNumber = value => {
      if (value === null || value === undefined) return "";
      // 使用toFixed方法将数字格式化为带两位小数的字符串
      return value.toFixed(2);
    };
    const truncateText = item => {
      if (item.checkLove % 2 == 1) return item.produceText.substring(0, 13) + "...";
      // 使用toFixed方法将数字格式化为带两位小数的字符串
      else return item.produceText;
    };
    const loveItem = item => {
      axios.get(`/api/updateLoveProduce?id=${item.produceId}`, {
        headers
      }).then(response => {
        // 请求成功，将获取的数据存储在 productList_1.value 数组中
        item.checkLove = !item.checkLove;
        show_6.value = false;
        console.log("数据收藏成功", response);
      }).catch(error => {
        console.error("Failed to fetch data:", error);
      });
    };
    return {
      formatNumber,
      icon,
      image,
      disPlay,
      show_1,
      show_2,
      show_3,
      show_4,
      show_5,
      show_6,
      goodItem,
      show_7,
      show_8,
      message,
      value,
      offset_produce,
      onClick,
      handleConfirm,
      handleCancel,
      checkConfirm,
      checkCancel,
      deleteItem,
      buyItem,
      produceList,
      onSearch,
      active,
      images,
      truncateText,
      loveItem,
      imageUrl1
    };
  }
};